<template>
  <v-container>
    <v-row>
      <v-col>
        <h1>{{ count }}</h1>
        <v-btn @click="increment">+</v-btn>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {};
  },
  computed: mapState(["count"]),
  methods: {
    increment() {
      this.$store.commit("increment");
    },
  },
};
</script>
